.todoInput {
  height: 40px;
  width: 100%;
  max-width: 80%;
  padding: 0px 10px;
  font-size: 16px;
  background-color: #343a40;
  color: #6b7179;
  border: none;
  border-radius: 5px;
}

.btn {
  width: 100%;
  max-width: 60px;
  float: right;
  height: 40px;
  background-color: #484f59;
  color: #fff;
  border: unset;
}
.btn:hover {
  cursor: pointer;
  opacity: 0.7;
}
