.todoItem {
  margin: 0px 0px 10px 0px;
  height: inherit;
}

.container {
  display: inline-block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #484f59;
  border-radius: 2px;
}

.container input:checked ~ .checkmark {
  background-color: #484f59;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.container input:checked ~ .checkmark:after {
  display: block;
}

.container .checkmark:after {
  left: 6px;
  top: 2px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.todoText {
  vertical-align: sub;
}

.removeBtn {
  float: right;
  background-color: transparent;
  color: red;
  border: unset;
  font-size: 18px;
}

.removeBtn:hover {
  cursor: pointer;
  opacity: 0.7;
}

.done {
  text-decoration: line-through;
}
